import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import {
  faSearch,
  faPlus,
  faEye,
  faEdit,
  faEllipsisH,
  faTrashAlt,
} from "@fortawesome/free-solid-svg-icons";
import {
  Col,
  Form,
  InputGroup,

  Card,
  Image,
  Table,
  Breadcrumb,
  Dropdown,
  Button,
  ButtonGroup,
} from "@themesberg/react-bootstrap";

import axios from "axios";
import MarkerClusterGroup from "react-leaflet-markercluster";
import ReactTooltip from "react-tooltip";
import L from "leaflet";
import { MapContainer, Popup, TileLayer, Marker } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import "leaflet-control-geocoder/dist/Control.Geocoder.js";
import "react-circular-progressbar/dist/styles.css";
import Swal from "sweetalert2";
import { useHistory } from "react-router-dom";

const icon = L.icon({
  iconSize: [30, 30],
  iconAnchor: [10, 41],
  popupAnchor: [2, -40],
  iconUrl: "https://i.ibb.co/dGQnpg3/t-l-chargement-1-removebg-preview.png",
});

const center = {
  lat: 45.424721,
  lng: -75.695,
};
const Getadmins = () => {
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const { data: response } = await axios.get(
          "http://localhost:3000/api/v1/users/getadmins"
        );
        setData(response);
        console.log("*******************", response);
      } catch (error) {
        console.error(error.message);
      }
      setLoading(false);
    };

    fetchData();
  }, []);

  const deleteClient = async (dm) => {
    try {
      Swal.fire({
        title: "Do you want to Delete this user?",
        showDenyButton: true,
        showCancelButton: true,
        confirmButtonText: "Delete",
        denyButtonText: `Don't Delete`,
      }).then(async (result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          setData(data.filter((c) => c._id !== dm._id));
          await axios
            .delete("http://localhost:3000/api/v1/users/" + dm._id)
            .then(Swal.fire("User Deleted!", "", "success"));
        } else if (result.isDenied) {
          Swal.fire("Changes are not saved", "", "info");
        }
      });
    } catch (error) {
      console.error(error.message);
    }
  };

  return (
    <>
         <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center">
        <div className="d-block mb-4 mb-md-0">
          <Breadcrumb
            className="d-none d-md-inline-block"
            listProps={{
              className: "breadcrumb-dark breadcrumb-transparent",
            }}
          ></Breadcrumb>
          <h4>Liste des cimitieres</h4>
{/*           <p className="mb-0">Your web analytics dashboard template.</p>

 */}       

  </div>
  
        <div className="btn-toolbar mb-2 mb-md-0">
          <ButtonGroup>
            <Dropdown.Toggle onClick={(e)=>history.push('/AdminAdd')} as={Button} variant="primary" size="sm" className="me-2">
            <FontAwesomeIcon icon={faPlus} className="me-2" />Nouvelle cimetiere
          </Dropdown.Toggle>
          </ButtonGroup>
        </div>
        
      </div>
      <div className="d-block mb-4 mb-md-2">
      <Col xs={8} md={6} lg={3} xl={4} >
            <InputGroup>
              <InputGroup.Text>
                <FontAwesomeIcon icon={faSearch} />
              </InputGroup.Text>
              <Form.Control type="text" placeholder="Search" />
            </InputGroup>
          </Col>
          </div>
      <Card border="light" className="shadow-sm mb-4">
        
        <Card.Body className="pb-0">
          <Table
            responsive
            className="table-centered table-nowrap rounded mb-0"
          >
            <thead className="thead-light">
              <tr>
                <th className="border-0"></th>
                <th className="border-0">Nom cimitiere</th>
                <th className="border-0">Respinsable</th>
                <th className="border-0">Télephone</th>
                <th className="border-0">Addresse </th>
                <th className="border-0">Actions</th>
              </tr>
            </thead>
            <tbody>
              {data?.map((dm, index) => (
                <>
                  <ReactTooltip />

                  <tr>
                    <td>
                      <Card.Link href="#" className="text-primary fw-bold">
                        <div className="user-avatar lg-avatar me-4">
                          <Image
                            style={{
                              flex: 1,
                              width: "50px",
                              height: "50px",
                              resizeMode: "contain",
                            }}
                            src={
                              "http://localhost:3000/uploads/" + dm.userimage
                            }
                            className="card-img-top rounded-circle border-white"
                          />
                        </div>
                      </Card.Link>
                    </td>
                    <td>{dm?.graveyard?.name}</td>

                    <td className="fw-bold">
                      {dm.name} {dm.lastn}
                    </td>
                    <td>{dm?.phone}</td>
                    <td data-tip={dm?.graveyard?.address}>
                      {dm?.graveyard?.address.substring(0, 30)}...
                    </td>
                    <td>
                      <Dropdown as={ButtonGroup}>
                        <Dropdown.Toggle
                          as={Button}
                          split
                          variant="link"
                          className="text-dark m-0 p-0"
                        >
                          <span className="icon icon-sm">
                            <FontAwesomeIcon
                              icon={faEllipsisH}
                              className="icon-dark"
                            />
                          </span>
                        </Dropdown.Toggle>
                        <Dropdown.Menu>
                          <Dropdown.Item
                            onClick={() =>
                              history.push(
                                "/GraveyardsProfiles/" + dm?.graveyard?._id
                              )
                            }
                          >
                            <FontAwesomeIcon icon={faEye} className="me-2" />{" "}
                            Liste des Clients
                          </Dropdown.Item>
                          <Dropdown.Item
                          onClick={() =>
                            history.push(
                              "/editgrave/" + dm?._id
                            )
                          }
                          >
                            <FontAwesomeIcon icon={faEdit} className="me-2" />{" "}
                            Editer
                          </Dropdown.Item>
                          <Dropdown.Item
                            className="text-danger"
                            onClick={(e) => {
                              deleteClient(dm);
                            }}
                          >
                            <FontAwesomeIcon
                              icon={faTrashAlt}
                              className="me-2"
                            />{" "}
                            Supprimer
                          </Dropdown.Item>
                        </Dropdown.Menu>
                      </Dropdown>
                    </td>
                  </tr>
                </>
              ))}
            </tbody>
          </Table>
        </Card.Body>
      </Card>
      {/*}
      <div className="center">
        <MapContainer center={center} zoom={13} scrollWheelZoom={true}>
          <TileLayer
            attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          <MarkerClusterGroup>
            {data?.map((dm, index) => (
              <Marker
                key={index}
                icon={icon}
                position={[dm?.graveyard?.Lat, dm?.graveyard?.Lng]}
              >
                <Popup>
                  <div
                    className="position-relative"
                    style={{ alignItems: "center" }}
                  >
                    <div></div>
                    <div>
                      <span
                        style={{
                          backgroundColor: "#3ba266",
                          fontSize: 18,
                          marginLeft: 100,
                        }}
                        color="#3ba266"
                        className="badge badge-success"
                      >
                        {dm?.graveyard?.name}
                      </span>
                    </div>
                  </div>
                  <Card.Body>
                    <Card.Subtitle
                      style={{ alignItems: "center", width: "150px" }}
                      className="mb-1"
                    >
                      {dm.Location}
                    </Card.Subtitle>
                    <Card.Text className="text-muted text-small mb-0 font-weight-light">
                      Address : {dm.graveyard?.address}
                    </Card.Text>
                    <Card.Text className="text-muted text-small mb-0 font-weight-light">
                      Responsable : {dm?.name}
                    </Card.Text>
                  </Card.Body>
                </Popup>
              </Marker>
            ))}
          </MarkerClusterGroup>
        </MapContainer>
        ,
      </div>
      {*/}
    </>
  );
};



export default Getadmins;
