import React, {
    useState,useEffect
  } from "react";
  import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
  import { faCalendarAlt, faEye, faEyeSlash } from "@fortawesome/free-solid-svg-icons";
  import "leaflet-control-geocoder/dist/Control.Geocoder.js";
  import {
    Col,
    Row,
    Card,
    Form,
    Button,
    InputGroup,
  } from "@themesberg/react-bootstrap";
  import { useHistory,useParams} from "react-router-dom";
  import MuiAlert from "@material-ui/lab/Alert";
  
  
  import axios from "axios";
  import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
  import L from "leaflet";
  import Swal from "sweetalert2";
  
  const Addadmin = () => {
   
    const [passwordType, setPasswordType] = useState("password");
    const [place, setPlace] = useState("");
    const [user, setUser] = useState({});
    let { id } = useParams();
    const history = useHistory();
    const togglePassword = () => {
      if (passwordType === "password") {
        setPasswordType("text");
        return;
      }
      setPasswordType("password");
    };


    async function getGrave() {
    
      try {
        const { data: response } = await axios.get(
          "http://localhost:3000/api/v1/users/"+ id
          );
        setUser(response);
      } catch (error) {
        console.error(error.message);
      }
  }
  useEffect(() => {
      getGrave() 
  }, []);
   
    const [formData, setFormData] = useState({
      name:user?.name,
      lastn: user?.lastn,
      Datebirth: user?.Datebirth,
      email: user?.email,
      sex: user?.sex,
      password: "",
      confirmPassword:"",
      userimage:user?.userimage,
      phone: user?.phone,
      role: "superadmin",
      gname: user?.graveyard?.name,
      address:user?.graveyard?.address,
    });
    const [birthday, setBirthday] = useState("");
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    async function Submit(e) {
      e.preventDefault();
      const mydata = new FormData();
      mydata.append("name", e.target[0].value);
      mydata.append("lastn", e.target[1].value);
      mydata.append("Datebirth", e.target[2].value);
      mydata.append("email", e.target[4].value);
      mydata.append("sex", e.target[3].value);
      mydata.append("phone", e.target[5].value);
      mydata.append("role", formData.role);

      /*     mydata.append("latitude", position.lat);
      mydata.append("logitude", position.lng); */
      const { data } = await axios.put(
        "http://localhost:3000/api/v1/users/editsuperadmin/" + id,
        mydata,
        config
      );
      console.log(data);
      if (data.status === 200) {
        Swal.fire({
          title: "Success",
          text: "User Updated",
          icon: "success",
          confirmButtonText: "OK",
        }).then((result) => {
          if (result.value) {
            history.push("/admin/superadmins");

    }
        });
      } else {
        Swal.fire({
          title: "Error",
          text: "User not Updated",
          icon: "error",
          confirmButtonText: "OK",
        })
      }
    }

    
  
    return (
      <Card border="light" className="bg-white shadow-sm mb-4">
        <Card.Body>
        <h5 className="mb-4">Update du super admin {user?.name}</h5>

          <h5 className="mb-4">General information</h5>
          <Form onSubmit={(e)=> Submit(e)}>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group id="firstName">
                  <Form.Label>First Name</Form.Label>
                  <Form.Control
                    
                    defaultValue={user?.name}
                    type="text"
                    placeholder="Enter your first name"
                    onChange={(e) =>
                      setFormData({ ...formData, name: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="lastName">
                  <Form.Label>Last Name</Form.Label>
                  <Form.Control
                    defaultValue={user?.lastn}
                    
                    type="text"
                    placeholder="Also your last name"
                    onChange={(e) =>
                      setFormData({ ...formData, lastn: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row className="align-items-center">
              <Col md={6} className="mb-3">
                <Form.Group id="birthday">
                  <Form.Label>Date de naissance</Form.Label>
                  <Col md={6} className="mb-3">
                    <Form.Group id="birthday">
                      <InputGroup>
                        <Form.Control
                          className="date"
                          name="Datedeath"
                          type="date"
                          defaultValue={user?.Datebirth}
                          onChange={(e) =>
                            setFormData({
                              ...formData,
                              Datebirth: e.target.value,
                            })
                          }
                        />
                      </InputGroup>
                    </Form.Group>
                  </Col>
                </Form.Group>
              </Col>
  
              <Col md={6} className="mb-3">
                <Form.Group id="gender">
                  <Form.Label>Gender</Form.Label>
                  <Form.Select
                    defaultValue={user?.sex}
                    onChange={(e) =>
                      setFormData({ ...formData, sex: e.target.value })
                    }
                  >
                    <option value="o">Other</option>
                    <option value="F">Female</option>
                    <option value="M">Male</option>
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group id="emal">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    
                    defaultValue={user?.email}
                    type="email"
                    placeholder="name@company.com"
                    onChange={(e) =>
                      setFormData({ ...formData, email: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="phone">
                  <Form.Label>Phone</Form.Label>
                  <Form.Control
                    
                    defaultValue={user?.phone}
                    type="number"
                    placeholder="(604) 555-5555"
                    onChange={(e) =>
                      setFormData({ ...formData, phone: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group id="firstName">
                  <Form.Label>Password</Form.Label>
                  <Form.Control
                    disabled
                    type="password"
                    placeholder="Enter your password"
                    onChange={(e) =>
                      setFormData({ ...formData, password: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="firstName">
                  <Form.Label> Confirmer votre mot de passe</Form.Label>
                  <InputGroup>
                    <Button
                    
                      size="sm"
                      style={{
                        borderColor: "transparent",
                        marginRight: 5,
                        backgroundColor: "#d7dce4",
                      }}
                      onClick={() => togglePassword()}
                    >
                      {passwordType === "password" ? (
                        <InputGroup.Text>
                          <FontAwesomeIcon icon={faEye} />
                        </InputGroup.Text>
                      ) : (
                        <InputGroup.Text>
                          <FontAwesomeIcon icon={faEyeSlash} />
                        </InputGroup.Text>
                      )}
                    </Button>
                    <Form.Control
                      type={passwordType}
                      disabled
                      placeholder="Confirmer votre mot de passe"
                      onChange={(e) =>
                        setFormData({
                          ...formData,
                          confirmPassword: e.target.value,
                        })
                      }
                    ></Form.Control>
                  </InputGroup>
                  {formData.confirmPassword !== formData.password &&
                  formData.confirmPassword !== "" ? (
                    <MuiAlert severity="warning">Password doesn't match</MuiAlert>
                  ) : (
                    <span></span>
                  )}
                </Form.Group>
              </Col>
  
              <Col md={6} className="mb-3">
                <Form.Group id="firstName">
                  <Form.Label>Image</Form.Label>
                  <Form.Control
                    
                    type="file"
                    placeholder="Enter your password"
                    onChange={(e) =>
                      setFormData({ ...formData, userimage: e.target.files[0] })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
  
            <div className="mt-3">
              <Button variant="primary" type="submit">
                Save All
              </Button>
            </div>
          </Form>
        </Card.Body>
      </Card>
    );
  };
  export default Addadmin;
  